<script>
import { Indicator } from 'mint-ui';
import html2canvas from 'html2canvas'
import commonLikes from "@/components/commonLikes"
import changeNick from '@/components/mainIndex/components/changeNick.vue'
import makeInfo from '@/components/mainIndex/components/makeInfo.vue'
import commonPl from "@/components/commonPl"
import { getPhone, UtilsMakeImg } from '@/utils'
// 初始数据
var initAddData = {
  nickName: '', //昵称
  makeSelfPl: '', //存储自定义评论
  yourHeaderImg: '', //头像
  contentStyle: '内容', //朋友圈形式(内容、链接)
  realContent: '', //朋友圈内容
  localtion: '', //地点
  realImgs: [], //朋友圈的图片们
  realImgsWid: '', //朋友圈的图片的宽
  realImgsHei: '', //朋友圈的图片的高
  smallImg: '', // 链接缩略图
  smallImgLink: 'http://www.baidu.com', // 链接网址
  smallContent: '', // 链接内容
  sendTime: '昨天', // 发送时间
  sendTimeNoformat: new Date().getTime(), // 发送时间(未格式化)
  likeNickName: [], //点赞昵称
  wordsAll: [],
  plMini: false,
  ifSelf: true //是否自己发送的

}
export default {
  components: { changeNick, makeInfo, commonLikes, commonPl },
  data() {
    return {
      showDiaTip: true,
      popupVisible6: false,
      useItemPl: false,
      tempPlData: {},
      ifShowBtn: false,
      popupVisiblecDz: false,
      tempLikes: [],
      dzDiaFlag: false,
      plDiaFlag: false,
      setOneImgClass: '',
      setInitMake: {},
      popupVisibletop: false,
      popupVisibleItem: false,
      dataURL: '',
      popupVisibleChangeItem: false,
      isFixed: false,
      popupVisibleReedit: false, //编辑朋友圈功能
      isSystemAz: false,
      offsetTop: 0,
      tempNickName: '',
      baseImgSizeClass: '',
      formInline: [{
        nickName: '示例昵称', //昵称
        makeSelfPl: '', //存储自定义评论
        yourHeaderImg: 'https://mrblackant.github.io/likeWechat/allImgs/za/62.jpg', //头像
        contentStyle: '内容', //朋友圈形式(内容、链接)
        realContent: '点我编辑当前朋友圈', //朋友圈内容
        localtion: '', //地点
        realImgs: [], //朋友圈的图片们
        realImgsWid: '', //朋友圈的图片的宽
        realImgsHei: '', //朋友圈的图片的高
        smallImg: '', // 链接缩略图
        smallImgLink: 'http://www.baidu.com', // 链接网址
        smallContent: '', // 链接内容
        sendTime: '昨天', // 发送时间
        sendTimeNoformat: new Date().getTime(), // 发送时间(未格式化)
        likeNickName: [], //点赞昵称
        wordsAll: [],
        plMini: false,
        ifSelf: true //是否自己发送的
      }],
      pyqItemIndex: '', //修改的朋友圈index
      pgShowBorder: false,
      nickForm: {
        yourHeaderImg: 'https://mrblackant.github.io/likeWechat/allImgs/za/145.jpg', //头像
        nickName: '点我编辑', //昵称
        bkImg: 'https://mrblackant.github.io/likeWechat/allImgs/za/145.jpg', //背景图
        noReadImg: 'https://mrblackant.github.io/likeWechat/allImgs/za/145.jpg', //未读头像
        noReadNum: 1, //未读数
        ifNoRead: true, //是否显示未读
        ifNewDt: false, //是否显示朋友的新动态
        firNewImg: 'https://mrblackant.github.io/likeWechat/allImgs/za/145.jpg', //朋友动态的头像
      }
    }
  },
  mounted() {
    // 获取手机型号
    this.isSystemAz = getPhone() === 'pg' ? false : true
    window.addEventListener('scroll', this.initHeight);
    this.$nextTick(() => {
      this.offsetTop = document.querySelector('#boxFixed').offsetTop;
    })
  },
  destroyed() {
    window.removeEventListener('scroll', this.initHeight)
  },
  methods: {
    routerBack() {
      this.showDiaTip=true
    },
    closeDialog() { //遮罩提示 显示控制
      this.showDiaTip = false
      // 操作区显示
      // this.ifShowBtn = true

    },
    goOn() { //继续制作
      // 图片清空,显示原制作页面
      this.dataURL = ''
      // 操作按钮显示
      this.ifShowBtn = true
    },
    loadding() { //加载loading
      Indicator.open({
        text: '努力生成中...',
        spinnerType: 'fading-circle'
      });
    },
    newBtn() {
      this.loadding()
      // 操作按钮消失
      this.ifShowBtn = false
      let _this = this
      // 延时 生成截图
      let t = setTimeout(() => {
        _this.shotCb()
      }, 300)
    },
    shotCb() {
      // 生成图片
      let p = UtilsMakeImg(html2canvas, 'makeCutScreen', this)
      p.then((canvas) => {
        let dataURL = canvas.toDataURL("image/png");
        // 得到图片url
        this.dataURL = dataURL;
      })

    },
    makeImg() {
      // 生成图片,弹窗展示
      this.ifShowBtn = true
    },
    btnPL(type) {
      // 评论change
      console.log(type)
      if (type === 'cha') {
        // 修改本条评论
        this.popupVisible6 = false //本体弹窗消失
        this.plDiaFlag = true //评论组件显示
        // 告诉评论组件显示我传过去的
        this.useItemPl = true
      } else {
        // 删除本条评论
        this.formInline[this.pyqItemIndex].wordsAll.splice(this.tempPlIndex, 1)
        this.popupVisible6 = false //本体弹窗消失
      }
    },
    getAllPl(data) {
      console.log(data)
      // 得到组件的评论内容
      let onePlArr = this.formInline[this.pyqItemIndex].wordsAll

      if (this.useItemPl) {
        // 修改某条单独的评论,单独替换
        onePlArr.splice(this.tempPlIndex, 1, data)
      } else {
        // 添加一条新的评论
        onePlArr.push(data)
      }
    },
    getLikesImg(data) {
      // 遍历展示点赞
      this.formInline[this.pyqItemIndex].likeNickName = data
    },
    dzContent(type) {
      // 修改、清除赞
      if (type === 'cha') {
        this.dzDiaFlag = true
      } else {
        // 清空
        this.formInline[this.pyqItemIndex].likeNickName = []
      }
      // 关闭自己
      this.popupVisiblecDz = false
    },
    closePl() {
      this.formInline[this.pyqItemIndex].plMini = false
    },
    chContent(type) {
      // 弹窗显示修改或者删除当前
      if (type === 'cha') {
        // 修改当前条，弹窗打开,确定修改了，再赋值
        this.setInitMake = (JSON.parse(JSON.stringify(this.formInline[this.pyqItemIndex])))
        this.popupVisibleReedit = true
      } else {
        // 删除当前条
        this.formInline.splice(this.pyqItemIndex, 1)
      }
      this.popupVisibleChangeItem = false
    },
    reeditItem(item, index) { //修改当前朋友圈item,弹窗显示
      this.pyqItemIndex = index
      this.popupVisibleChangeItem = true
    },
    getInfo(data) {
      console.log(data)
      console.log(data.realImgs)
      // 一张图片根据图片的宽高确定高度
      if (data.realImgs.length === 1) {
        // 根据图片的宽高来判断
        let imgW = data.realImgsWid
        let imgH = data.realImgsHei
        if (imgW > imgH) {
          console.log('宽>高')
          // 宽>高
          data.setOneImgClass = 'imgWid'
        } else {
          console.log('高>宽')
          data.setOneImgClass = 'imgHid'
        }
      }
      console.log(3)
      if (this.pyqItemIndex !== '') {
        //修改某条数据
        console.log('修改某条数据')
        this.formInline.splice(this.pyqItemIndex, 1, data)
      } else {
        // 新增
        console.log('新增')
        this.formInline.push(JSON.parse(JSON.stringify(data)))
      }

    },
    addPyqItems() {
      // 清空index,告诉页面不是修改
      this.pyqItemIndex = ''
      // 初始化数据
      this.setInitMake = JSON.parse(JSON.stringify(initAddData))
      //朋友圈内容增加 弹窗打开
      this.popupVisibleItem = true
    },
    addCotent(type) {
      // 选择增加还是清空
      switch (type) {
        case 'add':
          this.popupVisibleItem = false
          this.popupVisibleReedit = true
          break;
        case 'del':
          // 自己消失
          this.popupVisibleItem = false
          this.formInline = []
          break;
      }
    },
    setNickInfo(data) {
      // 拿到编辑的信息
      console.log(data)
      this.nickForm = data
    },
    changeBaseImg() {
      // 点击头像修改头像等
      this.popupVisibletop = true

    },
    initHeight() {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      this.isFixed = scrollTop > this.offsetTop ? true : false;
    },
    setPlZan(pyqItemIndex) {
      // 记录当前点击的是哪条朋友圈的inex
      this.pyqItemIndex = pyqItemIndex
      console.log(pyqItemIndex)
      console.log(this.formInline[this.pyqItemIndex].plMini)

      this.formInline[this.pyqItemIndex].plMini = true
    },
    changeContent() {},
    jumpUrl() {},
    jumpUrl() {},
    goDz() {
      let t = setTimeout(() => {
        // 点赞
        this.dzDiaFlag = true
        // 自己消失
        this.formInline[this.pyqItemIndex].plMini = false
      }, 100)
    },
    goPl() {
      // 不是修改某条评论，而是新增的
      this.useItemPl = false
      let t = setTimeout(() => {
        // 评论
        this.plDiaFlag = true
        this.tempNickName = this.formInline[this.pyqItemIndex].nickName
        // 自己消失
        this.formInline[this.pyqItemIndex].plMini = false
      }, 100)
    },
    changeDzInfo(index) {
      console.log('修改点赞')
      // 记录当前点击的是哪条朋友圈的inex
      this.pyqItemIndex = index

      this.tempLikes = this.formInline[index].likeNickName
      this.popupVisiblecDz = true
    },
    changePlContent(item, index, indexItem) {
      this.useItemPl = true

      // 单独的修改评论
      console.log('朋友圈第 ' + indexItem)
      // 记录当前点击的是哪条朋友圈的inex
      this.pyqItemIndex = indexItem
      this.tempNickName = this.formInline[this.pyqItemIndex].nickName
      // 评论弹窗显示
      this.popupVisible6 = true
      this.tempPlData = JSON.parse(JSON.stringify(item)) //临时保存评论
      console.log(item)
      console.log('评论的第几 ' + index)
      this.tempPlIndex = index //临时保存index
    },
  },
  watch: {
    dataURL() {
      if (this.dataURL) {
        Indicator.close();
      }
    },
    nickForm: {
      handler(newO, oldO) {
        // 判断是否展示未读、动态
        if (!newO.ifNewDt && newO.ifNoRead && !this.isSystemAz) {
          // 展示苹果对应的样式
          this.pgShowBorder = true
        } else {
          this.pgShowBorder = false
        }
      },
      deep: true
    }
  }
};

